<template>
    <div>
        <selectPar @getSelectId="getSelectId" :addMineSelect='addMineSelect' :addMineInput='addMineInput' />

    </div>
</template>

<script>
import selectPar from '@/components/selectPar.vue'
export default {
    name:'default',
    components: {
		selectPar
	},
    data(){
        return{
            addMineSelect:[],// 
            addMineInput:[],// 
        }
    },
    mounted(){
        this.setData()
    },
    watch:{
        "$route"(to,from){
            this.setData()
        }
    },
    methods:{
        // url123
        setData(){
            let urlParms =  window.location.hash.split('/')
            let id = urlParms.pop()
            switch(id){
                case '1' :
                    this.addMineSelect = []
                    this.addMineInput = []
                    break;
                case '2' :
                    this.addMineSelect = [
                        {
                            listName: '',
                            listData: [
                                {
                                    name: '',
                                    id: 'all',
                                },
                                {
                                    name: '',
                                    id: '0',
                                },
                                {
                                    name: '',
                                    id: '1',
                                },
                                {
                                    name: '',
                                    id: '2',
                                },
                            ],
                        },
                        {
                            listName: '',
                            listData: [
                                {
                                    name: '',
                                    id: 'all',
                                },
                                {
                                    name: '',
                                    id: '0',
                                },
                                {
                                    name: '',
                                    id: '1',
                                },
                                {
                                    name: '',
                                    id: '2',
                                },
                            ],
                        },
                    ]
                    this.addMineInput = []
                    break;
                case '3' :
                    this.addMineSelect = []
                    this.addMineInput = [
                        {
                            name:'', /// key
                            value:'', /// 
                            placeholder:'', /// 
                        },
                        {
                            name:'', /// key
                            value:'', /// 
                            placeholder:'', /// 
                        },
                    ]
                    break;
                default:
                    break;
            }
        },
        getSelectId(item){
            this.$message.success('')
        }
    }
}
</script>