import axios from 'axios'

/**
 	* @param options 
	* @param method 
	* @param url 
	* @param data 
	* @param headers 
*/

const http = options => {
	return new Promise((resolve, reject) => {
		const defaultOptions = {};	
		const newOptions = {
			...defaultOptions,
			...options
		};
		//headersjsontoken
		newOptions.headers = {
			...newOptions.headers
		};
		axios({
			method: newOptions.method,
			url: newOptions.url,
			data: newOptions.data,
			headers: newOptions.headers,
		}).then(res => {
			//
			if (res.status == 200) {
				//
			if(res.data.return_code == 0){
				resolve(res.data.data);
			}else{
				reject(res.data.return_msg)
			}
			} else {
				reject(res);
			}
		}).catch(err => {
			reject(err);
		})
	})
};

export {
http
}